export default {
    props: ['tabbarList'],
    data () {
        return {
            value: 0,
        }
    },
    methods:{
        change(){
            if(this.$route.path == "/call"){
                this.value = 0
            }
            if(this.$route.path == "/manage" || this.$route.path == "/turn"  || this.$route.path == "/check/apply"){
                this.value = 1
            }
            if(this.$route.path == "/statistics" || this.$route.path == "/check/turn" || this.$route.path == "/check/bossturn"){
                this.value = 2
            }
            if(this.$route.path == "/mine"){
                this.value = 3
            }
        }
    }
}



